import {EmojiButton} from "@joeattardi/emoji-button"
const socket= io('https://weonchat.herokuapp.com/');
const form = document.getElementById('send-container');
const messageInp = document.getElementById('messageInp');
const messagecontainer = document.querySelector('.message-container');
const nav=document.querySelector('.nav');

const picker = new EmojiButton({
    style: 'twemoji',
    // theme: 'dark',
    recentsCount: 20,
    // position: {
    //     bottom: '0',
    //     left: '0',
    //   }
    autoHide: false,
    // showCategoryButtons: false,
    //   showSearch: false,
    showPreview: false,
    //   showRecents: false,
    emojiSize: '24px',
    emojisPerRow: 6,
    rows: 6,
    autoFocusSearch:false,
    // showAnimation:false,
    // styleProperties: {
    //     '--font': 'Courier New',
    //     '--category-button-color': 'red',
    //     '--background-color':'grey',
    //     '--hover-color':'blue',
    //     '--text-color':'red',
    //   }
});
const trigger = document.querySelector('#emoji-button i');

picker.on('emoji', selection => {
  let newmessage=messageInp.value;
  newmessage+=selection.emoji;
  messageInp.value=newmessage;
});

trigger.addEventListener('click', (e) => {
    e.preventDefault();
    picker.togglePicker(trigger)
});

const messages = document.querySelector('.message');
const append=(Name,message,time,position)=>{
    const messageElement= document.createElement('div');
    messageElement.innerText= message;
    const messagesender=document.createElement('div');
    messagesender.innerText=Name;
    const messagetime=document.createElement('div');
    messagetime.innerText=time;
    messageElement.classList.add('message');
    messagesender.classList.add('nam');
    messagetime.classList.add('tame');
    messageElement.append(messagesender);
    messageElement.append(messagetime);
    messageElement.classList.add(position);
    messagecontainer.append(messageElement);
    $(".message-container").stop().animate({scrollTop:$(".message-container")[0].scrollHeight},1000);
}

var Time=()=>{
    var today = new Date();
    var time = today.getHours() + ":" + today.getMinutes();
    return time;
}

    // ***************for joined message*******************8
    const append1=(message,position)=>{
        const messageElement= document.createElement('div');
        messageElement.innerText= message;
        messageElement.classList.add('message1');
        messageElement.classList.add(position);
        messagecontainer.append(messageElement);
        $(".message-container").stop().animate({scrollTop:$(".message-container")[0].scrollHeight},1000);
    }
    
//    for data input from user
    const Name = prompt("Enter your Name here");
    const RoomId = prompt("Enter your RoomId here");
    // append2(RoomId);
    const data={Name,RoomId};
    // sending the data to the server
    socket.emit('new-user-joined',data);

    // for print the joined message
    socket.on('user-joined', Name=>{
        append1(`${Name} joined the chat`,'center')
    })

    // for print the message for myself
    form.addEventListener('submit',(e)=>{
        e.preventDefault();
        const message=messageInp.value;
        var time = Time();
        append('You', `${message}`,`${time}`,'right');
        socket.emit('send',message);
        messageInp.value='';
    
})
// for print the message for others
socket.on('recieve', data=>{
    var time=Time();
    append(`${data.name}`,`${data.message}`,`${time}`,'left');
})
// for print the leaving message
socket.on('left', name=>{
    append1(`${name} left the chat`,'center');
})